<template>
    <div class="container">
        <div class="money-info-page">
            <div class="pict-layout">
                <img src="../../static/img/b19204_217x136.png" alt="">
            </div>
            <div class="trans-fer">
                <ul>
                    <li>
                        <span>汇款金额:</span>
                        <input type="text" class="color" readonly :value="formData.money">
                    </li>
                    <li>
                        <span>汇款人金额:</span>
                        <input type="text" v-model="formData.name" placeholder="请输入姓名">
                    </li>
                </ul>
            </div>
            <div class="qinai">
                <p>亲爱的会员：</p>
                <p>请您留意：如果您在存款过程中支付失败，请您重新在存款页面发起新的存款操作，并输入不同的金额进行支付。感谢您的配合</p>
                <button @click="submitYhzz">填写完成，立即提交</button>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from "vuex";
    export default {
        data(){
            return {
                formData: {
                    money: 0,
                    name: '',
                },
                bankcardList: [],
                depositChannelList: [],
            }
        },
        watch: {
            new_name(new_value){
                var reg = /^[\u4e00-\u9fa5]+$/;
                var arr = new_value.split('')
                var new_arr = []
                arr.forEach(item=>{
                    if(reg.test(item) || item=='·'){
                        new_arr.push(item)
                    }
                })
                this.formData.name = new_arr.join('')
            },
        },
        computed:{
            new_name(){
                return this.formData.name
            },
            ...mapState('Recharge', {
                user: state => state.user,
                money: state => state.money,
            }),
        },
        created(){
            this.formData.money = this.money;

            this.getSystemBankCard();
            this.getEnums();
            this.getDepositChannel();
        },
        methods: {
            // 获取银行卡
            getSystemBankCard(){
                this.$api.recharge.getSystemBankCard(this.user)
                .then(res=>{
                    console.log(res);
                    this.bankcardList = res.bankcardList
                })
            },
            // 转入银行
            getEnums(){
                this.$api.recharge.getEnums()
                .then(res=>{
                    this.bankList = res.bankList
                })
            },
            // 转出银行
            getDepositChannel(){
                this.$api.recharge.getDepositChannel()
                .then(res=>{
                    this.depositChannelList = res.depositChannelList
                })
            },
            // 提交申请
            submitYhzz(){

                if(this.formData.name == ''){
                    this.$toast('请输入汇款人姓名');
                    return false;
                }

                this.$toast.loading({
                    duration: 0,
                    forbidClick: true,
                    message: '加载中',
                });

                var data = {
                    ...this.formData,
                    ...this.user,
                    money: this.money,
                    depositChannelId: this.depositChannelList[0]['id'],
                    bankCardId: this.bankcardList[0]['id'],
                    userBankName: this.bankList[0]['name'],
                }
                console.log(data);
                
                this.$api.recharge.submitYhzz(data)
                .then(res=>{
                    setTimeout(() => {
                        if(res.result==1){
                            this.$router.push('/transfer_two')
                        }else{
                            this.$toast(res.description)
                        }
                    }, 200);
                })
            },
        }
    }
</script>

